import '../App.css'
import { getPokemons } from '../services/pokemonService';
import { useEffect, useState } from 'react';
import ListPokemons from '../components/ListPokemons';
import { useFormStatus } from 'react-dom';

function Home() {
  const [pokemons, setPokemons] = useState(null)
  const [pagination, setPagination] = useState(null)

  const fetchPokemon = async (data) => {

    const pokemon = getPokemons(data.get("pokemonName"))
    setPokemons([pokemon])
  }

  useEffect(() => {
    const fetchPokemon = async () => {
      const fetch = await getPokemons(pagination)
      setPokemons(fetch)
    }
    fetchPokemon()
  }, [pagination])
  return (
    <div>
      <form action={fetchPokemon}>
        <Submit />
      </form>
      <ListPokemons pokemons={pokemons} setPagination={setPagination} />
    </div >
  )
}


const Submit = () => {
  const { pending } = useFormStatus();
  return (
    <div>
      <input name="pokemonName" type="text" placeholder="Write the pokemon name" />
      <button type='submit' disabled={pending}>Load More</button>
    </div>
  )
}


export default Home;
